import React, { Component, Suspense } from 'react';
import { Tab, Nav, Collapse, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { COPY_TO_CLIPBOARD } from '../../../Services/Utils/utils';
import NeedSupport from '../CommonComponents/NeedSupport';
const EmbedCode = React.lazy(() => import('../PublishCommonComponents/EmbedCode'));
const HashtagCampaignAside = React.lazy(() => import('./Components/HashtagCampaignAside'));
const Breadcrumbs = React.lazy(() => import('../CommonComponents/Breadcrumbs'));
const ShareEmbedModal = React.lazy(() => import('../PublishCommonComponents/ShareEmbedModal'));

class LandingPageCampaign extends Component {
    constructor() {
        super();
        this.state = { showModal:false, genrateCode: false, codeCopied: false };
    }
    shareEmbedModal = event => this.setState({
        showModal: true
    })
    shareEmbedCallback = (shareEmbedEvent) => {
        this.setState({
            showModal: shareEmbedEvent
        })
    }
    codeCopy = (id) => {
        this.setState({
            codeCopied: true
        })
        setTimeout(() => {
            this.setState({
                codeCopied: false
            }) 
        }, 1500);
        COPY_TO_CLIPBOARD(id);
    }
    render() {
        const { genrateCode, showModal, codeCopied } = this.state
        return  <div className="card mb-4">
                    <div className="card-body">
                        <div className="theme_preview bg-light rounded-2">
                            <iframe src="https://widget.taggbox.com/104917" style={{width:'100%',border:'none'}}></iframe>
                        </div>
                        <div className="block-center my-3">
                            <button type="button" className="btn btn-primary mb-2" onClick={() => this.setState({genrateCode:!genrateCode})}>
                                <span className="svg-icon">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                        <path fill="none" d="M0 0h24v24H0z"/>
                                        <path d="M24 12l-5.657 5.657-1.414-1.414L21.172 12l-4.243-4.243 1.414-1.414L24 12zM2.828 12l4.243 4.243-1.414 1.414L0 12l5.657-5.657L7.07 7.757 2.828 12zm6.96 9H7.66l6.552-18h2.128L9.788 21z" fill="currentColor"/>
                                    </svg>
                                </span>
                                Genrated Embed Code
                            </button>
                        </div>
                        <Collapse in={genrateCode}>
                            <Suspense fallback={''}>
                                <EmbedCode />
                            </Suspense>
                        </Collapse>
                        <div className="pt-2">
                            <Suspense fallback={''}>
                                <NeedSupport content={'Need Help?'} supportUrl={'https://player.vimeo.com'} videoUrl={'https://player.vimeo.com/video/510156339'} />
                            </Suspense>
                        </div>
                    </div>
                    <Suspense fallback={''}>
                        <ShareEmbedModal shareEmbedCallback={this.shareEmbedCallback} shareEmbedModal={showModal} />
                    </Suspense>
                </div>
    }
}

export default LandingPageCampaign;